const initialState = {
  authError: null,
};

const signIn = (state = initialState, action) => {
  switch (action.type) {
    case "LOGIN_SUCCESS":
      console.log("success");
      return { ...state, authError: null };
    case "LOGIN_ERROR":
      console.log("failed");
      return { ...state, authError: "Login failed" };
    case "SIGNOUT_SUCCESS":
      console.log("signout");
      return { ...state, authError: null };
    case "SIGNUP_SUCCESS":
      console.log("signup");
      return { ...state, authError: null };
    case "SIGNUP_ERROR":
      console.log(action.error);
      return { ...state, authError: action.error.message };
    default:
      return state;
  }
};

export default signIn;
